import { Container, Grid, Typography, Box, useTheme, Theme } from '@mui/material';
import React, { useState } from 'react';
import { animated, useSpring } from 'react-spring';
import FadeUp from '../Animated/FadeUp';
import BenefitCard from '../Cards/BenefitCard';
import { ImpressionArea } from '@lubycon/react';
import { grey } from '@mui/material/colors';

const activities = [
  {
    title: '',
    image: 'https://assets.lubycon.io/illusts/books.png',
    description: `            .\n      "           ".`,
  },
  {
    title: '',
    image: 'https://assets.lubycon.io/illusts/roadsign.png',
    description: `          .\n\n           .`,
  },
  {
    title: '',
    image: 'https://assets.lubycon.io/illusts/networking.png',
    description: `               .\n         .`,
  },
  {
    title: '',
    image: 'https://assets.lubycon.io/illusts/laptop.png',
    description:
      '          UI Kit         .',
  },
];

const ActivitySection = () => {
  const [visible, setVisible] = useState(false);
  const { spacing } = useTheme<Theme>();

  const titleAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
  });
  const descriptionAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
    delay: 300,
  });

  return (
    <Box
      component="section"
      sx={{
        padding: {
          md: `${spacing(15)} 0`,
          sm: `${spacing(10)} 0`,
        },
        backgroundColor: grey['100'],
      }}
    >
      <Container>
        <ImpressionArea onImpressionStart={() => setVisible(true)}>
          <animated.div style={titleAnimation}>
            <Typography
              variant="h4"
              component="h2"
              gutterBottom={true}
              sx={{
                fontWeight: 'bold',
                fontSize: {
                  md: 'h4.fontSize',
                  sm: 'h6.fontSize',
                },
              }}
            >
                
            </Typography>
          </animated.div>
          <Box component={animated.div} style={descriptionAnimation} sx={{ marginBottom: 5 }}>
            <Typography>
                 <strong> </strong>.      
                 .
              <br />
            </Typography>
          </Box>
          <Grid container spacing={2}>
            {activities.map(({ title, image, description }, index) => (
              <Grid key={index} item xs={12} sm={3}>
                <FadeUp>
                  <BenefitCard title={title} image={image} description={description} />
                </FadeUp>
              </Grid>
            ))}
          </Grid>
        </ImpressionArea>
      </Container>
    </Box>
  );
};

export default ActivitySection;
